class App {
	constructor() {
		this.init();
	}

	init() {
		$ = jQuery

		if (location.hash == '#thank-you') {
			$('#register__wrapper').hide();
			$('#thank-you').show();
		} else {
			$('#register__wrapper').show();
			$('#thank-you').hide();
		}

		$(window).on('hashchange', function (e) {
			if (location.hash == '#thank-you') {
				$('#register__wrapper').hide();
				$('#thank-you').show();
			} else {
				$('#register__wrapper').show();
				$('#thank-you').hide();
			}
		});

		$(document).ready(function () {
			$("#raw").addClass("hero__animation--fadeInUp");
			$("#wild")
				.delay(200)
				.queue("fx", function () {
					$(this).addClass("hero__animation--fadeInUp");
				});
			$("#sooke")
				.delay(500)
				.queue("fx", function () {
					$(this).addClass("hero__animation--fadeInUp");
				});
			$("#tagline")
				.delay(3000)
				.queue("fx", function () {
					// $("#raw").removeClass("hero__animation--fadeInUp")
					// $("#wild").removeClass("hero__animation--fadeInUp")
					// $("#sooke").removeClass("hero__animation--fadeInUp")

					$("#raw").remove()
					$("#wild").remove()
					$("#sooke").remove()

					$(".hero__content").addClass("hero__content--tagline")
					$(this).addClass("hero__animation--fadeInUp");
				});
		});
	}
}

new App(document.querySelector("main"), false);
